import React from "react"
import styled from "styled-components"
import { rhythm } from "../utils/typography"

const skills = [
  { name: "Python", level: 9, src: "/python.svg" },
  { name: "C", level: 8, src: "/c.svg" },
  { name: "JavaScript", level: 5, src: "/javascript.svg" },
  { name: "C++", level: 5, src: "/c++.svg" },
  { name: "C#", level: 5, src: "/c-sharp.svg" },
  { name: "Java", level: 5, src: "/java.svg" },
  { name: "HTML", level: 5, src: "/html-5.svg" },
  { name: "CSS", level: 5, src: "/css-3.svg" },
  { name: "SASS", level: 5, src: "/sass.svg" },
  { name: "Less", level: 5, src: "/less.svg" },
  { name: "Tailwind", level: 5, src: "/tailwindcss.svg" },
  { name: "Node.js", level: 5, src: "/nodejs.svg" },
  { name: "React", level: 5, src: "/react.svg" },
  { name: "Vue", level: 5, src: "/vue.svg" },
  { name: "Selenium", level: 5, src: "/selenium.svg" },
  { name: "Mocha", level: 5, src: "/mocha.svg" },
  { name: "Jest", level: 5, src: "/jest.svg" },
]

console.log({ rhythm: rhythm(1) })

const StyledSkillContainer = styled.div`
  .items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .item {
    border: 1px solid #7a7a7a;
    border-radius: 5px;
    width: calc(50% - 10px);
    height: ${rhythm(2)};
    margin: 5px;

    .skill {
      display: flex;
      flex-direction: row;
      padding: 0;
      margin: 5px;
      text-align: center;
      align-items: center;
      font-size: ${rhythm(0.75)};

      @media only screen and (max-width: 768px) {
        font-size: ${rhythm(0.5)};
      }

      div {
        display: flex;
        margin: 0 auto;

        p {
          padding: 0;
          margin: 0;
        }

        img {
          height: ${rhythm(0.7)};
          padding: 0;
          margin: ${rhythm(0.15)} 0;
        }
      }
    }

    .skill-bar {
      background: red;
      height: ${rhythm(0.2)};
      border-radius: ${rhythm(0.1)};
      margin: auto 5px;
      transition: width 0.3s ease;
      transition: 0.8s;
    }
  }
`

const Skills = ({ showBar }) => (
  <>
    <div className="items">
      {skills.map(({ name, level, src }, key) => (
        <div key={key} className="item">
          <div className="skill">
            <div>
              <img src={src} alt={name} /> <p>{name}</p>
            </div>
          </div>
          <div
            className="skill-bar"
            style={{
              width: !showBar ? `calc(calc(${level} * 10%) - 10px)` : 0,
            }}
          ></div>
        </div>
      ))}
    </div>
    <br />
  </>
)

export default ({ showBar }) => {
  return (
    <StyledSkillContainer>
      <p>Skill set that you can expect from me.</p>
      <Skills showBar={showBar} />
    </StyledSkillContainer>
  )
}
